@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}

.main_wraper{
  padding-top: 10px;
  background: -webkit-gradient( linear, left top, left bottom, from(#b2e7b8), to(#35D048));
  background: -webkit-linear-gradient( #b2e7b8, #35D048);
  background: linear-gradient( #b2e7b8, #35D048);
  width: 100%;
  height: 100vh;
  min-height: -webkit-fill-available;
}

.temp_size{
  font-size: 3rem
}
.sunrise{
  font-size: 1.2rem;
  height: 38px;
  background-image: url(/static/media/sun_rise.19c13612.png);
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: auto 100%;
}

.sunset{
  font-size: 1.2rem;
  height: 38px;
  background-image: url(/static/media/sun_set.78b5fefc.png);
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: auto 100%;
}
.weather_after{
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 110px;
  overflow: hidden;
  overflow-x: scroll;
  white-space: nowrap;
}
.WeatherAfter_atherWeatherChild__1dy-a{
    text-align: center;
    margin-right: 2px;
    width: 100px;
    height: 110px;
    background-color:#1d1d1d59;
    display: inline-block;
    font-size: 15px;
    overflow: auto;
}

.WeatherAfter_atherWeatherChild__1dy-a:hover{
  background-color:#81DAC6;
}

.WeatherAfter_atherWeatherChild__1dy-a.WeatherAfter_active__1pPpr{
  text-align: center;
  margin-right: 2px;
  width: 100px;
  height: 110px;
  background-color:#2EB597;
  z-index: 100;
  display: inline-block;
  font-size: 17px;
  cursor: pointer;
}

.WeatherAfter_icon__3sgza{
  width: 40px;
}
